import { Component, OnInit, Inject } from '@angular/core';
import { FormControl } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MAT_DIALOG_DATA } from '@angular/material';
import { ApiService } from 'src/app/shared/services/api.service';
import { PendingForAuthDetailsDTO } from 'src/app/shared/models/pending-for-auth-datails-dto';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-modal-transactions-detail-editable',
  templateUrl: './modal-transactions-detail-editable.component.html',
  styleUrls: ['./modal-transactions-detail-editable.component.scss']
})
export class ModalTransactionsDetailEditableComponent implements OnInit {
  items: number[] = [10, 20, 30, 40];
  name = new FormControl('');
  responseDto: PendingForAuthDetailsDTO;
  histDTO: PendingForAuthDetailsDTO;
saveButtonStatus: boolean;
  status: boolean;
  user_id: string;
  role: string;
  constructor(
    @Inject(MAT_DIALOG_DATA) private data,
    private snackBar: MatSnackBar,
    private api: ApiService
  ) { }

  ngOnInit() {

    this.responseDto = this.data;
    this.histDTO = this.data;
    this.user_id = sessionStorage.getItem('user_id');
    this.role = sessionStorage.getItem('user_role');
  }

  insertModifiedDataIntoDetailsData(reqDTO: PendingForAuthDetailsDTO) {
this.saveButtonStatus = true;
if (reqDTO.inputBy !== this.user_id) {
  Swal.fire('You Donot have permission for modification.','Only maker can modify record.');
  return;
}
this.api.sendUpdatedDataToDetailsData(reqDTO, this.user_id).subscribe(resp => {
      // console.log(resp);
      this.status = resp;
      console.log('insertion status: ' + this.status);
      if (this.status === true) {
        Swal.fire(
          'data saved successfully.',
          'success'
        );
      }
      if (this.status === false) {
        Swal.fire({
          type: 'error',
      
          text: 'data save to DB Failed.',
        });
      }
      // this.saveButtonStatus = false;
    });
}

  }

  // openSnackBar(message: string, action: string) {
  //   this.snackBar.open(message, action, {
  //     duration: 2000
  //   });
  // }
//}
